const paths = {
  plane:
    'M187.57,0C291.16,0,375.13,67.75,375.13,151.32c0,33.35-13.41,64.17-36.07,89.17l34.85,86.65L226.21,297.46c-15.43.42-32.72,4.16-50,4.61-2,.05-3.92.08-5.86.08C69.88,302.15,0,233.3,0,151.32,0,67.75,84,0,187.57,0',
  heart:
    'M322,32.32H305.46a.2.2,0,0,1-.2-.2V27.51A27.59,27.59,0,0,0,277.75,0h0a27.59,27.59,0,0,0-27.51,27.51v4.61a.2.2,0,0,1-.2.2H99.44a.2.2,0,0,1-.2-.2V27.51A27.59,27.59,0,0,0,71.73,0h0A27.59,27.59,0,0,0,44.22,27.51v4.61a.2.2,0,0,1-.2.2H27.51A27.59,27.59,0,0,0,0,59.82V320.48A27.59,27.59,0,0,0,27.51,348H322a27.59,27.59,0,0,0,27.51-27.51V59.82A27.59,27.59,0,0,0,322,32.32Zm-60.83-5a16.4,16.4,0,0,1,16.38-16.38h.2a16.63,16.63,0,0,1,16.59,16.59V75.67a16.63,16.63,0,0,1-16.59,16.59h-.2a16.4,16.4,0,0,1-16.38-16.38Zm-206,0A16.4,16.4,0,0,1,71.53,10.92h.2A16.63,16.63,0,0,1,88.31,27.51V75.67A16.63,16.63,0,0,1,71.73,92.26h-.2A16.4,16.4,0,0,1,55.14,75.88ZM322.19,326.14H27.3a5.54,5.54,0,0,1-5.46-5.46V128.94a.2.2,0,0,1,.2-.2H327.45a.2.2,0,0,1,.2.2V320.68A5.54,5.54,0,0,1,322.19,326.14Z,M595.68,1079.31c77.54-79.58,134.67-160.17,134.67-237.71,0-113.24-102-186.7-243.83-186.7-101,0-198,39-287,105.12H89v7.08H199.44l65.69,125.5c65.29-56.11,136.71-90.8,194.86-90.8,51,0,80.6,24.49,80.6,65.29,0,32.65-28.57,80.6-68.35,122.42L217.19,1259.88v117.32H751.77V1233.36h-301Z',
}
const text = {
  first: 'Refreshingly Simple Text Message Appointment Reminders',
  second: 'Calendar',
  third: 'Perfect Collection of business tools to help you grow',
}
const textIds = Object.keys(text)
const pathIds = Object.keys(paths)

const morphTransition = ({ from, to }) =>
  tween({
    from: 0,
    to: 1,
  }).pipe(interpolate(from, to))

const Icon = posed.path(
  pathIds.reduce((config, id) => {
    config[id] = {
      d: paths[id],
      transition: morphTransition,
    }

    return config
  }, {}),
)

const NextButton = posed.button({
  hoverable: true,
  pressable: true,
  init: { scale: 1 },
  hover: { scale: 1.1 },
  press: { scale: 0.9 },
})

class IconMorph extends React.Component {
  state = {
    pathIndex: 0,
    buttonText: text.first,
  }

  gotoNext = () => {
    const { pathIndex } = this.state
    const nextIndex = pathIndex + 1
    this.setState({
      pathIndex: nextIndex > pathIds.length - 1 ? 0 : nextIndex,
    })
  }

  render() {
    return (
      <StyledBody>
        <svg
          width="120"
          height="120"
          viewBox="0 0 375.13 327.15"
          fill="rgb(70, 200, 120)"
        >
          <Icon pose={pathIds[this.state.pathIndex]} />
        </svg>
        <NextButton onClick={this.gotoNext}>{this.state.buttonText}</NextButton>
      </StyledBody>
    )
  }
}

export default IconMorph
